import axios from 'axios';
export default {
  state: {
    caterories: []
  },
  getters: {
    allCategories(state) {
      return state.caterories.items ? state.caterories.items :[]
    },
  },
  mutations: {
    updateCaterories(state, caterories) {
      state.caterories = caterories
    }
  },
  actions: {
    loadCategories(context) {
      axios.get('https://vue-study.skillbox.cc/api/productCategories')
        .then(response => {
          context.commit('updateCaterories', response.data)
        })
    }
  }
}