import axios from 'axios';
export default {
  state: {
    cartProducts: []
  },
  getters: {
    allCartProducts(state) {
      if (state.cartProducts) {
        return state.cartProducts
      }
      else {
        return JSON.parse(localStorage.getItem('localStorageCarts'))
      }
    },
  },
  mutations: {
    updateCartProduct(state, cartProduct) {
      const localStorageCarts = JSON.parse(localStorage.getItem('localStorageCarts')) || [];
      const item = state.cartProducts.find(prod => prod.id === cartProduct.id)
      if (item) {
        item.amount = (item.amount + cartProduct.amount)
        const repCard = localStorageCarts.find(card => card.id === item.id)
        repCard.amount = (repCard.amount + cartProduct.amount)
        localStorageCarts.map(c => {
          if (c.id === repCard.id) {
            return repCard
          }
        })
        localStorage.setItem('localStorageCarts', JSON.stringify(localStorageCarts));
      }
      else {
        localStorageCarts.push(cartProduct)
        localStorage.setItem('localStorageCarts', JSON.stringify(localStorageCarts));
        state.cartProducts = localStorageCarts
      }
    },
    updateCartProductFromStorage(state) {
      if (JSON.parse(localStorage.getItem('localStorageCarts'))) {
        state.cartProducts = JSON.parse(localStorage.getItem('localStorageCarts'))
      }
    },
    changeCartItemAmount(state, { id, amount }) {
      const item = state.cartProducts.find(item => item.id === id)
      if (item) {
        item.amount = amount
      }
    },
    deleteProduct(state, { id }) {
      state.cartProducts = state.cartProducts.filter(item => item.id !== id)
    }
  },
  actions: {
    loadCartProduct(context, { cartProductId, amount }) {
      axios.get(`https://vue-study.skillbox.cc/api/products/${cartProductId}`, {
      })
        .then(response => response = response.data)
        .then(response => {
          response.amount = amount
          context.commit('updateCartProduct', response)
        })
    }
  }
}