import axios from 'axios';
export default {
  state: {
    product: {},
    productLoading: false,
  },
  getters: {
    currentProduct(state) {
      return state.product
    },
    isProductLoading(state) {
      return state.productLoading
    }
  },
  mutations: {
    updateProduct(state, product) {
      state.product = product
    },
    changeProductLoading(state) {
      state.productLoading = !state.productLoading
    }
  },
  actions: {
    loadProduct(context, { productId }) {
      context.commit('changeProductLoading')
      setTimeout(() => {
        axios.get(`https://vue-study.skillbox.cc/api/products/${productId}`, {
        })
          .then(response => {
            context.commit('updateProduct', response.data)
            context.commit('changeProductLoading')
          })
      }, 300);

    }
  }
}