<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
    </div>
    <div class="content__catalog">
      <ProductsFilter v-model:minPrice="minPrice" v-model:maxPrice="maxPrice" v-model:categoryId="categoryId" v-model:colorId="colorId"/>
      <section class="catalog">
        <ProductsList />
        <ProductsPagination :totalPages="totalPages" v-model="page" />
      </section>
    </div>
  </main>
</template>

<script>
import ProductsFilter from '@/components/ProductsFilter.vue';
import ProductsList from '@/components/ProductsList.vue';
import ProductsPagination from '@/components/ProductsPagination.vue';
import { mapGetters } from 'vuex';
export default {
  components: {
    ProductsFilter,
    ProductsList,
    ProductsPagination
  },
  data() {
    return {
      perPage: 4,
      page: 1,
      maxPrice: 0,
      minPrice: 0,
      categoryId: 0,
      colorId: 0
    }
  },
  computed: {
    ...mapGetters(['totalPages'])
  },
  methods: {
    loadCards() {
      this.$store.dispatch('loadProducts',
        { perPage: this.perPage, page: this.page, minPrice: this.minPrice, maxPrice: this.maxPrice, categoryId: this.categoryId, colorId: this.colorId })
    },

  },
  mounted() {
    this.loadCards()
  },
  watch: {
    page() {
      this.loadCards()
    },
    minPrice() {
      this.loadCards()
    },
    maxPrice() {
      this.loadCards()
    },
    categoryId() {
      this.loadCards()
    },
    colorId() {
      this.loadCards()
    }
  }
}
</script>

<style lang="scss" scoped></style>