<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item" @click.prevent="prevPage" :class="{ 'disabled': modelValue === 1 }">
      <a class="pagination__link pagination__link--arrow" aria-label="Предыдущая страница">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>

    <li class="pagination__item" v-for="pageNum in totalPages" :key="pageNum" @click="changePage(pageNum)">
      <a class="pagination__link " :class="{ 'pagination__link--current': pageNum === modelValue }">
        {{ pageNum }}
      </a>
    </li>

    <li class="pagination__item" @click.prevent="nextPage" :class="{ 'disabled': modelValue === totalPages }">
      <a class="pagination__link pagination__link--arrow" href="#" aria-label="Следующая страница">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>

export default {
  props: {
    totalPages: {
      type: Number,
      required: true,
      default: 0
    },
    modelValue: {
      type: Number,
      required: true,
      default: 0
    }
  },
  methods: {
    changePage(pageNum) {
      this.$emit('update:modelValue', pageNum)
    },
    nextPage() {
      this.$emit('update:modelValue', this.modelValue + 1)
    },
    prevPage() {
      this.$emit('update:modelValue', this.modelValue - 1)
    },
  }
}
</script>
<style  scoped>
.pagination__item {
  cursor: pointer;
}

.disabled {
  pointer-events: none;
}
</style>