<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="product.image.file.url" width="120" height="120" :alt="product.title">
    </div>
    <h3 class="product__title">
      {{ product.title }}
    </h3>
    <span class="product__code">
      Артикул: {{ product.id }}
    </span>
    <div class="product__counter form__counter">
      <button type="button" aria-label="Убрать один товар" @click.prevent="decreaseAmount">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>
      <input type="number" @input="checkValue($event)" v-model.number="product.amount" name="count">
      <button type="button" aria-label="Добавить один товар" @click.prevent="increaseAmount">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>
    <b class="product__price">
      {{ product.price * product.amount }} ₽
    </b>
    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click="deleteProduct">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
export default {
  props: {
    product: Object
  },
  computed: {
    amount: {
      get() {
        return this.product.amount
      },
      set(modelValue) {
        if (modelValue > 1) {
          this.$store.commit('changeCartItemAmount', { id: this.product.id, amount: +modelValue })
        }
        else {
          this.$store.commit('changeCartItemAmount', { id: this.product.id, amount: 1 })
        }
      }
    },

  },
  methods: {
    changeLocalStorage(item) {
      const localStorageCarts = JSON.parse(localStorage.getItem('localStorageCarts')) || [];
      const localStorageItem = localStorageCarts.find(item => item.id === this.product.id)
      localStorageItem.amount = this.product.amount + item
      localStorageCarts.map(item => {
        if (item.id === localStorageItem.id) {
          return localStorageItem
        }
      })
      localStorage.setItem('localStorageCarts', JSON.stringify(localStorageCarts));
    },

    decreaseAmount() {
      if (this.product.amount > 1) {
        this.changeLocalStorage(-1)
        this.$store.commit('changeCartItemAmount', { id: this.product.id, amount: this.product.amount -= 1 })
      }
      else {
        this.$store.commit('changeCartItemAmount', { id: this.product.id, amount: 1 })
      }

    },
    increaseAmount() {
      this.changeLocalStorage(1)
      this.$store.commit('changeCartItemAmount', { id: this.product.id, amount: this.product.amount += 1 })
    },
    checkValue(e) {
      const localStorageCarts = JSON.parse(localStorage.getItem('localStorageCarts')) || [];
      const localStorageItem = localStorageCarts.find(item => item.id === this.product.id)
      localStorageItem.amount = +e.target.value
      if (e.target.value < 2) {
        this.product.amount = 1
        localStorageItem.amount = 1
      }
      localStorageCarts.map(item => {
        if (item.id === localStorageItem.id) {
          return localStorageItem
        }
      })
      localStorage.setItem('localStorageCarts', JSON.stringify(localStorageCarts));

    },
    deleteProduct() {
      this.$store.commit('deleteProduct', { id: this.product.id })
      let localStorageCarts = JSON.parse(localStorage.getItem('localStorageCarts')) || [];
      localStorageCarts = localStorageCarts.filter(item => item.id !== this.product.id)
      localStorage.setItem('localStorageCarts', JSON.stringify(localStorageCarts));
    }
  }
}
</script>

<style >
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0;
  /* <-- Apparently some margin are still there even though it's hidden */
}

input[type='number'] {
  -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}
</style>