import axios from 'axios';
export default {
  state: {
    colors: []
  },
  getters: {
    allColors(state) {
      return state.colors.items ? state.colors.items : []
    },
  },
  mutations: {
    updateColors(state, colors) {
      state.colors = colors
    }
  },
  actions: {
    loadColors(context) {
      axios.get('https://vue-study.skillbox.cc/api/colors')
        .then(response => {
          context.commit('updateColors', response.data)
        })
    }
  }
}