import axios from 'axios';
export default {
  state: {
    products: [],
    productsLoading: false,
  },
  getters: {
    allProducts(state) {
      return state.products
    },
    totalPages(state) {
      return state.products.pagination ? state.products.pagination.pages : 0
    },
    isLoading(state) {
      return state.productsLoading
    }
  },
  mutations: {
    updateProducts(state, products) {
      state.products = products
    },
    changeLoading(state) {
      state.productsLoading = !state.productsLoading
    }
  },
  actions: {
    loadProducts(context, { perPage, page, minPrice, maxPrice, categoryId, colorId }) {
      context.commit('changeLoading')
      setTimeout(() => {
        axios.get('https://vue-study.skillbox.cc/api/products', {
          params: {
            page,
            limit: perPage,
            minPrice,
            maxPrice,
            categoryId,
            colorId
          }
        })
          .then(response => {
            context.commit('updateProducts', response.data)
            context.commit('changeLoading')
          })
      }, 600);

    }
  }
}