<template>
  <aside class="filter">
    <h2 class="filter__title">Фильтры</h2>

    <form class="filter__form form" action="#" method="get" @submit.prevent="filter">
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>
        <label class="form__label form__label--price">
          <input class="form__input" type="number" name="min-price" @input="priceFrom($event)" :value="minPrice">
          <span class="form__value">От</span>
        </label>
        <label class="form__label form__label--price">
          <input class="form__input" type="number" name="max-price" @input="priceTo($event)" :value="maxPrice">
          <span class="form__value">До</span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Категория</legend>
        <label class="form__label form__label--select">
          <select class="form__select" type="text" name="category" @change="selectCaterogy($event)">
            <option value="value1">Все категории</option>
            <option v-for="category in allCategories" :key="category.id" :value="category.id">
              {{ category.title }}</option>
          </select>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Цвет</legend>
        <ul class="colors">
          <li class="colors__item" v-for="color in allColors" :key="color.id">
            <label class="colors__label" >
              <input class="colors__radio sr-only" type="radio" name="color" @click="selectColor($event)" :value="color.id">
              <span class="colors__value" :style="{ backgroundColor: color.code }">
              </span>
            </label>
          </li>
        </ul>
      </fieldset>
      <button class="filter__submit button button--primery" type="submit">
        Применить
      </button>
      <button class="filter__reset button button--second" type="button" @click.prevent="resetFilter">
        Сбросить
      </button>
    </form>
  </aside>
</template>

<script>
import { mapGetters } from 'vuex';
export default {
  data() {
    return {
      filterMinprice: 0,
      filterMaxPrice: 0,
      filterCategory: 0,
      filterColor: 0,
    }
  },
  props: {
    minPrice: Number,
    maxPrice: Number,
    categoryId: Number,
    colorId: Number,
  },
  computed: {
    ...mapGetters(['allCategories', 'allColors']),
  },
  methods: {
    priceFrom(event) {
      this.filterMinprice = event.target.value
    },
    priceTo(event) {
      this.filterMaxPrice = event.target.value
    },
    selectCaterogy(event) {
      this.filterCategory = event.target.value
    },
    selectColor(event) {
      this.filterColor = event.target.value
    },
    filter() {
      this.$emit('update:maxPrice', this.filterMaxPrice)
      this.$emit('update:minPrice', this.filterMinprice)
      this.$emit('update:categoryId', this.filterCategory)
      this.$emit('update:colorId', this.filterColor)
    },
    resetFilter() {
      this.$emit('update:maxPrice', 0)
      this.$emit('update:minPrice', 0)
      this.$emit('update:categoryId', 0)
      this.$emit('update:colorId', 0)
    }
  },
  mounted() {
    this.$store.dispatch('loadCategories',)
    this.$store.dispatch('loadColors')
  },


}
</script>

<style lang="scss" scoped></style>