<template>
  <Preloader v-if="isProductLoading" />
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ 'name': 'MainPage' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ 'name': 'MainPage' }">
            {{ currentProduct.category.title }}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{ currentProduct.title }}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img width="570" height="570" :src="currentProduct.image.file.url" :alt="currentProduct.title">
        </div>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{ currentProduct.id }}</span>
        <h2 class="item__title">
          {{ currentProduct.title }}
        </h2>
        <div class="item__form">
          <form class="form" action="#" method="POST">
            <b class="item__price">
              {{ currentProduct.price }} ₽
            </b>

            <fieldset class="form__block">
              <legend class="form__legend">Цвет:</legend>
              <ul class="colors">
                <li class="colors__item" v-for="color in currentProduct.colors" :key="color.id">
                  <label class="colors__label">
                    <input class="colors__radio sr-only" type="radio" name="color-item" :value="color.code">
                    <span class="colors__value" :style="{ backgroundColor: color.code }">
                    </span>
                  </label>
                </li>
              </ul>
            </fieldset>

            <div class="item__row">
              <div class="form__counter">
                <button type="button" aria-label="Убрать один товар" @click.prevent="decreaseAmount">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>

                <input v-model="prodAmount" @input="checkValue" type="number" name="count">

                <button type="button" aria-label="Добавить один товар" @click.prevent="increaseAmount">
                  <svg width="12" height="12" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>

              <button class="button button--primery" type="submit" @click.prevent="addToCart">
                В корзину
              </button>
            </div>
          </form>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import { mapGetters } from 'vuex';
import Preloader from '@/components/helpers/Preloader.vue';
export default {
  data() {
    return {
      prodAmount: 1
    }
  },
  components: {
    Preloader
  },
  methods: {
    addToCart() {
      this.$store.dispatch('loadCartProduct', { cartProductId: this.$route.params.id, amount: this.prodAmount })
    },
    increaseAmount() {
      this.prodAmount += 1
    },
    decreaseAmount() {
      if (this.prodAmount < 2) {
        return
      }
      else {
        this.prodAmount -= 1
      }
    },
    checkValue() {
      if (this.prodAmount < 2) {
        this.prodAmount = 1
      }
    }
  },
  computed: {
    ...mapGetters(['currentProduct', 'isProductLoading'])
  },
  created() {
    this.$store.dispatch('loadProduct', { productId: this.$route.params.id })
  }
}
</script>

<style scoped>
.item__pics {
  max-width: 80%;
}

button {
  cursor: pointer;
}
</style>