<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{'name':'MainPage'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" v-if="allCartProducts.length > 0">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem v-for="product in allCartProducts" :key="product.id" :product="product"/>
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Ура, вы набрали целую корзину товаров! &#129321;
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice }} ₽</span>
          </p>
          <button disabled class="cart__button button button--primery" type="submit">
            Оформить заказ нелья <br>
            Это тестовая работа &#128515;
          </button>
        </div>
      </form>
      <h1 v-else>Товаров нет &#128554;</h1>
    </section>
  </main>
</template>

<script>
import CartItem from '@/components/CartItem.vue';
import { mapGetters } from 'vuex';
export default {
  components:{
    CartItem
  },
  computed: {
    ...mapGetters(['allCartProducts']),
    totalPrice() {
      return this.allCartProducts.reduce((currentSum, item) => {
        return currentSum + (item.price * item.amount)
      }, 0)
    }
  },
}
</script>

<style lang="scss" scoped></style>